.count,
.ordered,
.submit {
}
h1 {
  text-align: center;
}

.param {
  display: flex;
  justify-content: space-around;
  margin: 5px;
}
.func {
  display: flex;
  margin: 5px;
}

.editor {
  flex-grow: 2;
}

.out {
  min-height: 100px;
  margin: 5px;
}

.out-header, .out-line {
  display: flex;
  width: 100%;
}
.out-header > div,
.out-line > div {
  padding: 5px;
  border-width: medium;
  border-color: #333;
  border-style: solid;
  width: 13%;
}

.out-line > div {
  overflow-wrap: break-word;
}

